    //汇总专家意见
    hQ(){
        let _this = this
        //判断是否所有专家已经审核（包含通过和不通过）
        let flag = false
        _this.loadingBtn2 = true
        for(let i=0;i<_this.expertInfoArr.length;i++){
            let obj  = _this.expertInfoArr[i]
            if(obj.isAudit==0&&obj.isLeader>1){
                flag = true
                break
            }

        }
        let formartData = new FormData()
        formartData.append("agreeType",_this.expertAdvice.agreeType)
        formartData.append("auditOpinon",_this.expertAdvice.auditOpinon)
        formartData.append("id",_this.expertAdvice.id)
        formartData.append("auditFileId",_this.expertAdvice.auditFileId)

        if(_this.expertAdvice.agreeType==""){
                this.$message({
                  showClose: true,
                  message: '请选择汇总意见',
                  type: 'warning'
                });
              _this.loadingBtn2 = false
              return false
        }

        if(flag){
              this.$confirm('还有专家没有填写评审意见, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                  hZ(formartData).then(response=>{
                      _this.loadingBtn2 = false
                      if(response.data.successful){
                          this.$message({
