    //处理导出ceb文件结果
		downloadCeb(data, fileName) {
			console.log(data);
			const link = document.createElement('a');
			let blob = new Blob([data], { type: 'application/octet-stream' });
			link.style.display = 'none';
			link.href = URL.createObjectURL(blob);
			link.setAttribute('download', fileName);
			document.body.appendChild(link);
			link.click();
			document.body.removeChild(link);
		},
    //下载附件
    downloadFile(data){
      let _this = this
       exportReport({"feaAuditid":data.AUDIT_FILE_ID}).then(response=>{
          let filename = "评审意见信息.doc"
          _this.loading = false
          console.log('download res',response);
          //通过后台返回 的word文件流设置文件名并下载
          var blob = new Blob([response.data], { type: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document;charset=utf-8'}); //application/vnd.openxmlformats-officedocument.wordprocessingml.document这里表示doc类型
          var downloadElement = document.createElement('a');
          var href = window.URL.createObjectURL(blob); //创建下载的链接
          downloadElement.href = href;
          downloadElement.download =filename; //下载后文件名
          document.body.appendChild(downloadElement);
          downloadElement.click(); //点击下载
          document.body.removeChild(downloadElement); //下载完成移除元素
          window.URL.revokeObjectURL(href); //释放掉blob对象
       })
    },
