                  type: 'warning'
                });
            _this.loadingBtn1 = false
            return false
        }
        formData.append("id", _this.confirmParam.id)
        formData.append("auditJoinType",_this.confirmParam.auditJoinType)
        formData.append("auditJoinOpinon",_this.confirmParam.auditJoinOpinon)
        confirmGoRewiew(formData).then(response=>{
             if(response.data.successful){
               _this.confirmParam = {
                  "id":"",
                  "auditJoinType":"1",
                  "auditJoinOpinon":""
                }
                this.$message({
                  showClose: true,
                  message: '确认成功',
                  type: 'success'
                });
                _this.getTable(_this.currentPage,_this.currentNum)
                _this.dialogFormVisible = false
                _this.loadingBtn1 = false
             }else{
                this.$message({
                  showClose: true,
                  message: '确认失败',
                  type: 'error'
                });
                _this.loadingBtn1 = false
             }
        })
    },
    cancelBtn() {
      let _this =this
      _this.dialogFormVisible = false
      _this.$nextTick(()=>{
        // debugger
        _this.expertAdvice.agreeType = ""
        _this.expertAdvice.auditOpinon = ""
        _this.$refs.uploadFile.clearFiles()
        _this.expertAdvice.auditFileId = ""
      })
    },
    //显示操作弹框
    showDialog(data){
      let _this =this
      _this.dialogFormVisible = true
      _this.dialogLoading =true
      
      let itemId = data.itemId
      if(data.itemPhaseDetT=='10'&&data.isJoin==0){   //选择评审专家  scope.row.itemPhaseDetT== '7'&&scope.row.isJoin==0
          _this.dialogTitle = "确认是否参加评审"
          _this.dialogShowType = "1"
          _this.tempFeaAuditid = data.feaAuditid
          _this.confirmParam.auditJoinOpinon = ""
          //判断是否为自动选择，如果自动选择，则允许不参加
          if(data.isAutoSelect==0){
              _this.confirmParam.isAutoSelect = true
          }else{
              _this.confirmParam.isAutoSelect = false
          }

      }else if(data.itemPhaseDetT=='12'&&data.isJoin>0&&data.isPassed==0){ //汇总专家评审意见   scope.row.itemPhaseDetT== '7'&&scope.row.isJoin==1&&scope.row.isPassed==0
          _this.dialogTitle = "汇总专家评审意见"
          _this.dialogShowType = "2"
          // _this.submitAdvice.itemId = data.itemId
          _this.expertAdvice.id = data.feaAuditid
          _this.itemSpeciality = data.specialityT
          //打开弹窗清空数据
          _this.expertAdvice.auditOpinon = ""
          _this.expertAdvice.agreeType = ""
          _this.$nextTick(()=>{
            _this.$refs.uploadFile.clearFiles()
            _this.expertAdvice.auditFileId = ""
          })
          //查询专家列表
          findExpertsAdvice({"itemId":itemId}).then(response=>{
              if(response.data.successful){
                  console.log(response.data)
                  let labelArr = response.data.resultValue.tableLable
                  let arr = []
                  for(let index in labelArr){
                     let obj = labelArr[index]
                      if(obj.colDname=="professional"){
                            arr = obj.codeList
                      }
                  }
                  _this.expertInfoArr = response.data.resultValue.tableData
                  _this.expertInfoArrShow = []
                  _this.expertInfoArr.forEach(function(item){
                     let obj = {}
                     obj = item

                      if(item.isAudit==1){
                          obj.isAuditStr == "通过"
                      }else if(item.isAudit==0){
                          obj.isAuditStr == "未审核"
                      }else if(item.isAudit==2){
                          obj.isAuditStr == "不通过"
                      }
                      for(let i=0;i<arr.length;i++){
                          let temp1 = arr[i]
                          if(obj.professional==temp1.codeValue){
                              obj.professional = temp1.codeName
                          }
                      }
                      
                      _this.expertInfoArrShow.push(obj)
                  })
              }else{
                 this.$message({
                    showClose: true,
                    message: '查询专家列表失败',
                    type: 'error'
                  });
                  _this.expertInfoArr = []
              }
          })

      }else if(data.itemPhaseDetT=='17'){
         _this.dialogTitle = "上传正式评审文件"
          _this.dialogShowType = "4"
          _this.submitAdvice.itemId = data.itemId
          _this.itemSpeciality = data.specialityT
      } 
    },
     // 页面主表格获取当前每页条数
     handleSizeChange(val) {
      this.currentNum = val;
      this.getTable(this.currentPage,val)
    },
    //获取当前页面页码
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getTable(val,this.currentNum)
    },
    getTable(page,size){
      let _this = this
      _this.loading = true
      let paramSelect = {}
      paramSelect.index = page
      paramSelect.pageSize = size
      paramSelect.isLeader = '1'
      // paramSelect.itemPhaseDet = '6'
      if(_this.param.isPassed==0){//查询代办
          // paramSelect.auditStatus = '6,7,8,9,10'
          paramSelect.isAudit = '0'
      }
      paramSelect.itemPhaseDet = _this.param.itemPhaseDet
      paramSelect.itemName = _this.param.itemName
      paramSelect.createOrg = _this.param.deptCode
      paramSelect.itemLevel = _this.param.itemLevel
      paramSelect.speciality = _this.param.speciality
      
      groupLeaderList(paramSelect).then(response => {//{}写需要的参数
          console.log('response',response)//后端返回数据
          if(response.data.successful){
              _this.totalNum = response.data.resultValue.totalNum
              _this.tableLabel = response.data.resultValue.tableLable
              let arr = response.data.resultValue.tableLable
              _this.tableLabel = _this.getLabelList(arr)
              _this.dictMap = _this.getDictArr(arr)
              _this.columnTypeMap = _this.getColumnTypeArr(arr)
              _this.tableData = response.data.resultValue.tableData
              _this.tableShowData = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)

          }
          _this.loading = false
          
        }).catch(error => {
          console.log(error)//error
        })
        
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          console.log(key + ':' + value);
          if(key=="itemPhaseDet"){
               copyObj["itemPhaseDetT"] = value;
          }
          if(key=="speciality"){
              copyObj["specialityT"] = value;
          }
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="DATE"){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
    //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
     //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
     //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y+'-'+this.add0(m)+'-'+this.add0(d);
    },
    add0(m){
      return m<10?'0'+m:m;
    },
    beforeUploadReport(file) {
			if (file.name) {
				let pex = file.name.split('.')[1];
				if (pex == 'doc' || pex == 'docx') {
					return true;
				} else {
					this.$message.warning('文件格式不正确,只能上传doc,docx文件!');
					return false;
				}
			} else {
				this.$message.warning('文件格式不正确,只能上传doc,docx文件!');
				return false;
			}
		},
  }
}
</script>
